.switch {
  background-color: transparent;
  box-sizing: border-box;
  width: 36px;
  height: 36px;
  border: 2px solid red;
  border-radius: 50%;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  overflow: hidden;
}

.switch.gray {
  border-color: #bbbbbb;
}

.switch.yellow {
  border-color: #d5d832;
}

.switch.gray .switch__image {
  stroke: #bbbbbb;
}

.switch.yellow .switch__image {
  stroke: #d5d832;
}

.switch:hover.gray {
  background-color: #bbbbbb;
}

.switch:hover.yellow {
  background-color: #d5d832;
}

.switch:hover .switch__image {
  stroke: #111111;
}

.switch:disabled {
  cursor: default;
  background-color: #bbbbbb;
}

.switch:disabled .switch__image {
  stroke: #111111;
}
